import React from 'react';
import PropTypes from 'prop-types';
import styled from 'styled-components';
import { CardMedia } from '@material-ui/core';
import { SMALL_CARD } from '../../constants';
import GlobalTheme from '../../styled/GlobalTheme';

const Container = styled.div`
  width: ${SMALL_CARD};
  height: 242px;
  margin: 15px;
  text-align: left;
  border-radius: 8px;
  transform: ${({ translate, type }) => (type === 'left' ? `translateX(${-translate}px)` : `translateX(${translate}px)`)};
  transition: ${({ transition }) => (`transform ease-out ${transition}s`)};
  ${GlobalTheme.breakpoints.down('md')}{
    width: 180px;
    height: 180px;
  }
  ${GlobalTheme.breakpoints.down('sm')}{
    width: 140px;
    height: 140px;
  }
`;

const CardMediaStyled = styled(CardMedia)`
  && {
    width: ${SMALL_CARD};
    height: 200px;
    border-radius: 8px;
    cursor: pointer;
    ${GlobalTheme.breakpoints.down('md')}{
      width: 180px;
      height: 142px;
    }
    ${GlobalTheme.breakpoints.down('sm')}{
      width: 140px;
      height: 120px;
    }
  }
`;

const CardText = styled.div`
  width: ${SMALL_CARD};
  display: flex;
  justify-content: center;
  ${GlobalTheme.breakpoints.down('md')}{
    width: 180px;
  }
  ${GlobalTheme.breakpoints.down('sm')}{
    width: 140px;
  }
`;

const Title = styled.div`
width: 183px;
height: 83px;
display: flex;
justify-content: center;
text-align: center;
box-shadow: 0px 6px 12px rgba(0, 0, 0, 0.1);
border-radius: 8px;
align-items: center;
margin-top: -50px;
background: ${({ theme }) => theme.colors.ui01};
padding: 0 20px;
font-weight: bold;
font-size: 14px;
cursor: pointer;
&:hover {
  box-shadow: 0px 3px 15px rgba(0, 0, 0, 0.25);
}
${GlobalTheme.breakpoints.down('md')}{
  width: 160px;
  height: 60px;
  margin-top: -30px;
}
${GlobalTheme.breakpoints.down('sm')}{
  width: 120px;
  height: 60px;
  font-size: 12px;
}
`;


const SmallCard = ({ item, effect, history }) => {
  const { translate, transition, type } = effect;
  const redirectService = (id) => {
    history.push(`/service/${id}`);
  };
  return (
    <Container
      key={item.id}
      translate={translate}
      type={type}
      transition={transition}
      onClick={() => redirectService(item.id)}
    >
      <CardMediaStyled
        image={item.url}
        title="image"
      />
      <CardText>
        <Title>
          {item.name}
        </Title>
      </CardText>
    </Container>
  );
};
SmallCard.propTypes = {
  item: PropTypes.shape({
    id: PropTypes.number,
    name: PropTypes.string,
    url: PropTypes.string,
    description: PropTypes.string,
    userFullName: PropTypes.string,
    remainingDays: PropTypes.number,
  }).isRequired,
  effect: PropTypes.shape({
    translate: PropTypes.number,
    transition: PropTypes.number,
    type: PropTypes.string,
  }).isRequired,
  history: PropTypes.instanceOf(Object).isRequired,
};

export default SmallCard;
