import React, { useState } from 'react';
import { FormattedMessage } from 'react-intl';
import { makeStyles } from '@material-ui/core/styles';
import {
  AppBar, Toolbar, Typography, Button, IconButton,
} from '@material-ui/core';
import MenuIcon from '@material-ui/icons/Menu';
import CloseIcon from '@material-ui/icons/Close';
import SearchRoundedIcon from '@material-ui/icons/SearchRounded';
import MailOutlineRoundedIcon from '@material-ui/icons/MailOutlineRounded';
import DrawerMenu from './DrawerMenu';
import colors from './utils/colors';
// import { Link } from 'react-router-dom';


const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  iconsButton: {
    marginRight: theme.spacing(2),
  },
  title: {
    flexGrow: 1,
    fontFamily: 'Pacifico',
  },
}));

const Nav = () => {
  const classes = useStyles();
  const width = window.innerWidth;
  const getLogo = (screenWidth) => (screenWidth > 320 ? 'Mirall' : 'M');
  const [isOpen, setIsOpen] = useState(false);

  const toggleMenu = () => {
    setIsOpen(!isOpen);
  };

  return (
    <div className={classes.root}>
      <AppBar
        position="fixed"
        color={colors.grayWhite}
        elevation={0}
      >
        <Toolbar>
          <IconButton
            edge="start"
            className={classes.iconsButton}
            color="inherit"
            aria-label="menu"
            onClick={() => { toggleMenu(); }}
          >
            {isOpen ? <CloseIcon /> : <MenuIcon /> }
          </IconButton>
          <DrawerMenu isOpen={isOpen} toggleMenu={toggleMenu} />
          <Typography align="left" variant="h6" className={classes.title}>
            { getLogo(width) }
          </Typography>
          <IconButton edge="start" className={classes.iconsButton} color="inherit" aria-label="search">
            <SearchRoundedIcon />
          </IconButton>
          <IconButton edge="start" className={classes.iconsButton} color="inherit" aria-label="send email">
            <MailOutlineRoundedIcon />
          </IconButton>
          <Button variant="contained" color="secondary">
            <FormattedMessage
              id="nav.login"
              defaultMessage="Ingresar"
            />
          </Button>
        </Toolbar>
      </AppBar>
    </div>
  );
};

export default Nav;
