/* eslint-disable react/no-array-index-key */
import React from 'react';
import PropTypes from 'prop-types';
import styled from 'styled-components';
import { FormattedMessage } from 'react-intl';
import { Typography } from '@material-ui/core';
import textsStyles from '../utils/textsStyles';
import GlobalTheme from '../../styled/GlobalTheme';

const Container = styled.div`
  ${GlobalTheme.breakpoints.down('sm')}{
    width: 100%;
  }
`;

const HourGrid = styled.div`
  display: flex;
  flex-direction: column;
  height: 320px;
  align-items: center;
  ${GlobalTheme.breakpoints.down('sm')}{
    height: auto;
  }
`;

const Hour = styled.div`
  font-size: 12px;
  width: 104px;
  box-shadow: ${({ theme }) => (theme.elevations.e03)};
  border-radius: 4px;
  padding: 10px 6px;
  text-align: center;
  background: ${({ theme, selectedHour }) => (selectedHour ? theme.colors.blueSkyBrand : theme.colors.ui01)};
  margin-bottom: 15px;
  z-index: 1;
  ${({ selectedHour, theme }) => (selectedHour ? `color: ${theme.colors.textInverse};` : '')}
  ${({ selectedHour }) => {
    if (!selectedHour) {
      return (
        `&:hover {
          box-shadow: 0px 3px 15px rgba(0, 0, 0, 0.25);
          color: ${({ theme }) => theme.colors.hoverPrimary};
          cursor: pointer;
        }`
      );
    }
    return '';
  }}
  ${GlobalTheme.breakpoints.down('sm')}{
    width: 100%;
  }
`;

const Hours = (props) => {
  const {
    hours,
    activeIndexHour,
    setActiveIndexHour,
  } = props;
  const textsClasses = textsStyles();

  const getSetHour = (hour, i) => {
    setActiveIndexHour(i);
  };

  return (
    <Container>
      <Typography className={textsClasses.heading5} style={{ textAlign: 'center', margin: '36px 0 20px 0' }} component="div">
        <FormattedMessage
          id="schedule.hour"
          defaultMessage="Hora"
        />
      </Typography>
      <HourGrid>
        {hours.map((hour, i) => (
          <Hour
            key={i}
            selectedHour={i === activeIndexHour}
            onClick={() => getSetHour(hour, i)}
          >
            {`${hour.startHour} - ${hour.endHour}`}
          </Hour>
        ))}
      </HourGrid>
    </Container>
  );
};
Hours.propTypes = {
  hours: PropTypes.instanceOf(Array).isRequired,
  activeIndexHour: PropTypes.number.isRequired,
  setActiveIndexHour: PropTypes.func.isRequired,
};
export default Hours;
