import React from 'react';
import { withStyles } from '@material-ui/core/styles';
import { FormattedMessage } from 'react-intl';
import {
  Typography,
  Button,
} from '@material-ui/core';
import styled from 'styled-components';
import MuiDialogContent from '@material-ui/core/DialogContent';
import PropTypes from 'prop-types';
import textsStyles from '../utils/textsStyles';
import colors from '../utils/colors';
import dotGrid from '../../images/dotGrid.svg';
import GlobalTheme from '../../styled/GlobalTheme';

const DotGird = styled.img`
  margin: 0 0 -145px 365px;
  ${GlobalTheme.breakpoints.down('sm')}{
    display: none;
  }
`;

const TextBox = styled.textarea`
  background: ${({ theme }) => (theme.colors.ui01)};
  width: 436px;
  height: 155px;
  border: 1px solid ${({ theme }) => (colors.ui05)};
  border-radius: 4px;
  margin-top: 50px;
  color:  ${({ theme }) => (theme.colors.text02)};
  padding: 10px;
  z-index: 1;
  ${GlobalTheme.breakpoints.down('sm')}{
    margin-top: 20px;
    width: calc(100% - 20px);
  }
`;

const DialogContent = withStyles((theme) => ({
  root: {
    padding: '35px 50px',
    textAlign: 'center',
    position: 'relative',
  },
}))(MuiDialogContent);

const Goal = (props) => {
  const {
    setTab,
  } = props;
  const textsClasses = textsStyles();
  const handleContinue = () => {
    setTab(3);
  };

  return (
    <DialogContent>
      <Typography className={textsClasses.heading4} style={{ textAlign: 'center' }} component="div">
        <FormattedMessage
          id="schedule.goal"
          defaultMessage="Objetivo"
        />
      </Typography>
      <Typography
        className={textsClasses.body3}
        style={{ textAlign: 'center', color: colors.text02 }}
        component="div"
      >
        <FormattedMessage
          id="schedule.goal.description"
          defaultMessage="Explícanos brevemente lo que esperas obtener"
        />
      </Typography>
      <DotGird
        alt="Dot grid"
        src={dotGrid}
      />
      <TextBox />
      <Typography
        className={textsClasses.body2}
        style={{
          textAlign: 'center',
          color: colors.text02,
          paddingTop: '10px',
          marginBottom: '59px',
        }}
        component="div"
      >
        <FormattedMessage
          id="schedule.goal.description.intructor"
          defaultMessage="Esta información se enviará al instructor para que pueda personalizar tu experiencia y brindarte un mejor servicio."
        />
      </Typography>
      <Button
        variant="contained"
        onClick={handleContinue}
        style={{
          padding: '10px 32px',
          color: colors.textInverse,
          backgroundColor: colors.blueSkyBrand,
        }}
        className={textsClasses.heading5}
        color="primary"
      >
        <FormattedMessage
          id="global.continue"
          defaultMessage="Continuar"
        />
      </Button>
    </DialogContent>
  );
};

Goal.propTypes = {
  setTab: PropTypes.func.isRequired,
};

export default Goal;
