import React, { useState } from 'react';
import { withStyles } from '@material-ui/core/styles';
import {
  Dialog,
  IconButton,
} from '@material-ui/core';
import styled from 'styled-components';
import MuiDialogTitle from '@material-ui/core/DialogTitle';
import CloseIcon from '@material-ui/icons/Close';
import PropTypes from 'prop-types';
import Schedule from './Schedule';
import Goal from './Goal';
import SelectCard from './SelectCard';
import NumCard from './NumCard';
import NameCard from './NameCard';
import DateCard from './DateCard';
import BackCard from './BackCard';
import Summary from './Summary';
import {
  SCHEDULE_STEP,
  GOAL_STEP,
  SELECT_CARD_STEP,
  NUMBER_CARD_STEP,
  NAME_CARD_STEP,
  DATE_CARD_STEP,
  BACK_CARD_STEP,
} from './constants';
import GlobalTheme from '../../styled/GlobalTheme';

const DialogStyled = styled(Dialog)`
  && {
    .MuiPaper-root{
      ${GlobalTheme.breakpoints.down('sm')}{
        width: 100%;
        margin: 0;
        max-height: 100%;
        border-radius: 0;
        height: 100%;
      } 
    }
  }
`;

const styles = (theme) => ({
  closeButton: {
    position: 'absolute',
    right: theme.spacing(1),
    top: theme.spacing(1),
  },
});

const DialogTitle = withStyles(styles)((props) => {
  const {
    classes,
    onClose,
  } = props;
  return (
    <MuiDialogTitle style={{ height: '15px' }}>
      {onClose ? (
        <IconButton aria-label="close" className={classes.closeButton} onClick={onClose}>
          <CloseIcon />
        </IconButton>
      ) : null}
    </MuiDialogTitle>
  );
});

const Handler = (props) => {
  const {
    isOpen,
    setIsOpen,
    schedule,
  } = props;
  const handleClose = () => {
    setIsOpen(false);
  };
  const [tab, setTab] = useState(1);
  const [creditCardInfo, setCreditCardInfo] = useState({
    number: '',
    name: '',
    date: '',
    cvc: '',
  });
  const addSpace = (value) => {
    const result = value.replace(/\W/gi, '').replace(/(.{4})/g, '$1 ');
    return result.slice(0, -1);
  };
  //   const [clientSckechule, setClientSchedule] = useState(null);
  return (
    <DialogStyled
      onClose={handleClose}
      aria-labelledby="customized-dialog-title"
      open={isOpen}
      fullWidth
      maxwidth="lg"
    >
      <DialogTitle id="customized-dialog-title" onClose={handleClose} />
      {tab === SCHEDULE_STEP && (
      <Schedule
        setIsOpen={setIsOpen}
        schedule={schedule}
        setTab={setTab}
      />
      )}
      {tab === GOAL_STEP && (
      <Goal
        setTab={setTab}
      />
      )}
      {tab === SELECT_CARD_STEP && (
      <SelectCard
        setTab={setTab}
      />
      )}
      {tab === NUMBER_CARD_STEP && (
      <NumCard
        setTab={setTab}
        creditCardInfo={creditCardInfo}
        setCreditCardInfo={setCreditCardInfo}
      />
      )}
      {tab === NAME_CARD_STEP && (
      <NameCard
        setTab={setTab}
        creditCardInfo={creditCardInfo}
        setCreditCardInfo={setCreditCardInfo}
        addSpace={addSpace}
      />
      )}
      {tab === DATE_CARD_STEP && (
      <DateCard
        setTab={setTab}
        creditCardInfo={creditCardInfo}
        setCreditCardInfo={setCreditCardInfo}
        addSpace={addSpace}
      />
      )}
      {tab === BACK_CARD_STEP && (
      <BackCard
        setTab={setTab}
        creditCardInfo={creditCardInfo}
        setCreditCardInfo={setCreditCardInfo}
      />
      )}
      {tab === 8 && (
        <Summary
          onClose={handleClose}
          setTab={setTab}
        />
      )}
    </DialogStyled>
  );
};

Handler.propTypes = {
  isOpen: PropTypes.bool.isRequired,
  setIsOpen: PropTypes.func.isRequired,
  schedule: PropTypes.instanceOf(Array).isRequired,
};

export default Handler;
