import React, { useState } from 'react';
import { withStyles } from '@material-ui/core/styles';
import { FormattedMessage, useIntl } from 'react-intl';
import {
  Typography,
  Button,
} from '@material-ui/core';
import styled from 'styled-components';
import MuiDialogContent from '@material-ui/core/DialogContent';
import PropTypes from 'prop-types';
import textsStyles from '../utils/textsStyles';
import selectedCardStyles from './selectedCardStyles';
import colors from '../utils/colors';
import cardNumber from '../../images/cardNumber.svg';
import GlobalTheme from '../../styled/GlobalTheme';

const CardImageBox = styled.div`
  position: relative;
  width: 360px;
  height: 207px;
  margin: 20px 0;
  ${GlobalTheme.breakpoints.down('sm')}{
    height: auto;
  }
`;

const CardImage = styled.img`
  ${GlobalTheme.breakpoints.down('sm')}{
    width: 275px;
  }
`;

const InputBox = styled.div`
  text-align: left;
  width: 300px;
  ${GlobalTheme.breakpoints.down('sm')}{
    width: 100%;
  }
`;

const Input = styled.input`
  padding: 16px;
  color: ${({ theme }) => (theme.colors.text03)};
  border: 1px solid ${({ theme }) => (theme.colors.ui05)};
  border-radius: 4px;
  font-size: 12px;
  width: 300px;
  ${GlobalTheme.breakpoints.down('sm')}{
    width: 240px;
  }
`;


const DialogContent = withStyles((theme) => ({
  root: {
    padding: '35px 50px',
    textAlign: 'center',
    position: 'relative',
  },
}))(MuiDialogContent);

const NumCard = (props) => {
  const {
    setTab,
    creditCardInfo,
    setCreditCardInfo,
  } = props;
  const intl = useIntl();
  const textsClasses = textsStyles();
  const classes = selectedCardStyles();
  const [isActive, setIsActive] = useState(false);
  const handleContinue = () => {
    setCreditCardInfo({
      ...creditCardInfo,
      number: creditCardInfo.number.replace(/-/g, ''),
    });
    setTab(5);
  };
  const addDash = (value) => {
    const result = value.replace(/\W/gi, '').replace(/(.{4})/g, '$1-');
    return result.slice(0, -1);
  };
  const handleChangeNumber = (event) => {
    let { value } = event.target;
    value = value.replace(/-/g, '');
    if (/^(\s*|\d+)$/.test(value) && value.length <= 16) {
      if (value.length === 16) {
        value = addDash(value);
        setIsActive(true);
      }
      setCreditCardInfo({
        ...creditCardInfo,
        number: value,
      });
    }
  };
  return (
    <DialogContent className={classes.cardGrid}>
      <Typography className={textsClasses.heading4} style={{ textAlign: 'center' }} component="div">
        <FormattedMessage
          id="schedule.card.register"
          defaultMessage="Registrar Tarjeta"
        />
      </Typography>
      <Typography
        className={textsClasses.body3}
        style={{ textAlign: 'center', color: colors.text02 }}
        component="div"
      >
        <FormattedMessage
          id="schedule.card.details"
          defaultMessage="Introduce los detalles de tu tarjeta"
        />
      </Typography>

      <CardImageBox>
        <CardImage
          alt="Card Number"
          src={cardNumber}
        />
      </CardImageBox>
      <InputBox>
        <Typography
          className={textsClasses.body5}
          component="div"
        >
          <FormattedMessage
            id="schedule.card.number"
            defaultMessage="Número de tarjeta"
          />
        </Typography>
        <Input
          type="text"
          name="number"
          onChange={handleChangeNumber}
          placeholder={intl.formatMessage({
            id: 'schedule.card.number.input',
            defaultMessage: 'Escribe tu número de tarjeta',
          })}
          value={creditCardInfo.number}
        />
      </InputBox>
      <Button
        variant="contained"
        onClick={handleContinue}
        style={{
          margin: '35px 0px',
          padding: '10px 32px',
          color: colors.textInverse,
          backgroundColor: (
            isActive
          )
            ? colors.blueSkyBrand
            : colors.noSelectedBSB,
        }}
        className={textsClasses.heading5}
        color="primary"
        disabled={!isActive}
      >
        <FormattedMessage
          id="global.continue"
          defaultMessage="Continuar"
        />
      </Button>
    </DialogContent>
  );
};

NumCard.propTypes = {
  setTab: PropTypes.func.isRequired,
  creditCardInfo: PropTypes.instanceOf(Object).isRequired,
  setCreditCardInfo: PropTypes.func.isRequired,
};

export default NumCard;
