/* eslint-disable jsx-a11y/no-noninteractive-element-interactions */
/* eslint-disable jsx-a11y/click-events-have-key-events */
/* eslint-disable react/no-array-index-key */
import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import { Typography } from '@material-ui/core';
import KeyboardArrowRightSharpIcon from '@material-ui/icons/KeyboardArrowRightSharp';
import KeyboardArrowLeftSharpIcon from '@material-ui/icons/KeyboardArrowLeftSharp';
import moment from 'moment';
import 'moment/locale/es';
import calendarStyles from './calendarStyles';
import textsStyles from '../utils/textsStyles';
import { MONTHS_CALENDAR } from '../../constants';
import capitalize from '../utils/helpers';

const Calendar = (props) => {
  const {
    schedule,
    setHours,
    setActiveIndexHour,
    activeIndexDay,
    setActiveIndexDay,
  } = props;
  moment.updateLocale();
  const classes = calendarStyles();
  const textClasses = textsStyles();
  const TODAY = moment().format('D');
  const [months, setMonths] = useState([]);
  const [monthKey, setMonthKey] = useState(0);
  const weekDaysShort = moment.weekdaysMin().map((day) => capitalize(day));

  const getSelectedDaysInMonth = () => {
    const isoweekdaysRepeted = schedule.map((day) => day.isoWeekday);
    const isoweekdays = [];
    isoweekdaysRepeted.forEach((day) => {
      if (isoweekdays.indexOf(day) === -1) {
        isoweekdays.push(day);
      }
    });
    let selectedDaysInMoth = [];
    isoweekdays.forEach((isoDay) => {
      const firstDay = moment().add(monthKey, 'month').startOf('month');
      const firstIsoday = moment().add(monthKey, 'month').startOf('month').isoWeekday();
      const month = firstDay.format('M');
      let dayToAdd = 0;
      if (firstIsoday <= isoDay) {
        dayToAdd = isoDay - firstIsoday;
      } else {
        dayToAdd = (7 - firstIsoday) + isoDay;
      }
      let day = firstDay.add(dayToAdd, 'days');
      while (day.format('M') === month) {
        selectedDaysInMoth.push(parseInt(day.format('D'), 10));
        day = day.add(7, 'days');
      }
    });
    if (monthKey === 0) {
      selectedDaysInMoth = selectedDaysInMoth.filter((day) => day >= TODAY);
    }
    return selectedDaysInMoth;
  };
  const getMonths = () => {
    const m = [];
    for (let i = 0; i < MONTHS_CALENDAR; i += 1) {
      const month = moment().add(i, 'month').format('MMMM');
      m.push(capitalize(month));
    }
    return m;
  };
  const startDay = moment().add(monthKey, 'month').startOf('month').format('d');
  const totalDaysMonth = moment().add(monthKey, 'month').daysInMonth();
  useEffect(() => {
    setMonths(getMonths());
  }, []);
  const nextMonth = () => {
    if (monthKey + 1 < MONTHS_CALENDAR) {
      setMonthKey(monthKey + 1);
    }
  };
  const prevMonth = () => {
    if (monthKey > 0) {
      setMonthKey(monthKey - 1);
    }
  };
  const getHours = (day) => {
    const date = moment().add(monthKey, 'month').format(`YYYY-MM-${day}`);
    const isoDay = moment(date, 'YYYY-MM-D').isoWeekday();
    const totalHours = schedule
      .filter((dayS) => dayS.isoWeekday === isoDay)
      .map((dayS) => ({ startHour: dayS.startHour, endHour: dayS.endHour }));
    setActiveIndexDay(day);
    setHours(totalHours);
    setActiveIndexHour(-1);
  };
  const weekdays = weekDaysShort.map((day) => (
    <td key={day} className={`${classes.weekdaysHeader} ${textClasses.heading5}`}>
      {day}
    </td>
  ));
  const trDays = () => {
    const blank = [];
    const daysInMonth = [];
    const rows = [];
    let cells = [];
    for (let i = 0; i < startDay; i += 1) {
      blank.push(
        <td key={i * 50}>
          {' '}
        </td>,
      );
    }
    const selectedDays = getSelectedDaysInMonth();
    for (let day = 1; day <= totalDaysMonth; day += 1) {
      let tdDay;
      if (selectedDays.indexOf(day) !== -1) {
        tdDay = (
          <td
            key={day}
            onClick={() => getHours(day)}
            className={day === activeIndexDay ? classes.activeDay : classes.selectedDay}
          >
            {day}
          </td>
        );
      } else {
        tdDay = (
          <td key={day} className={classes.noSelectedDay}>
            {day}
          </td>
        );
      }
      daysInMonth.push(tdDay);
    }
    const totalDays = [
      ...blank,
      ...daysInMonth,
    ];
    totalDays.forEach((row, i) => {
      if ((i % 7) !== 0) {
        cells.push(row);
      } else {
        const insertRow = cells.slice();
        rows.push(insertRow);
        cells = [];
        cells.push(row);
      }
      if (i === totalDays.length - 1) {
        const insertRow = cells.slice();
        rows.push(insertRow);
      }
    });
    return rows.map((d, i) => (
      <tr key={i * 100}>
        {d}
      </tr>
    ));
  };
  return (
    <Typography className={classes.calendar} component="div">
      <Typography className={classes.monthTitle} component="div">
        <KeyboardArrowLeftSharpIcon onClick={prevMonth} fontSize="small" />
        <Typography style={{ margin: '0 25px' }} className={textClasses.heading5} component="div">
          { months[monthKey] }
        </Typography>
        <KeyboardArrowRightSharpIcon onClick={nextMonth} fontSize="small" />
      </Typography>
      <Typography style={{ width: '100%', height: '100%' }} component="div">
        <table style={{ width: '100%', height: '100%', padding: '0 0 25px 0' }}>
          <tbody>
            <tr>
              {weekdays}
            </tr>
            {trDays()}
          </tbody>
        </table>
      </Typography>
    </Typography>
  );
};

Calendar.propTypes = {
  schedule: PropTypes.instanceOf(Array).isRequired,
  setHours: PropTypes.func,
  setActiveIndexHour: PropTypes.func,
  activeIndexDay: PropTypes.number,
  setActiveIndexDay: PropTypes.func,
};
Calendar.defaultProps = {
  setHours: () => {},
  setActiveIndexHour: () => {},
  activeIndexDay: -1,
  setActiveIndexDay: () => {},
};

export default Calendar;
