import React from 'react';
import { withStyles } from '@material-ui/core/styles';
import {
  Typography,
  Button,
} from '@material-ui/core';
import { FormattedMessage } from 'react-intl';
import MuiDialogContent from '@material-ui/core/DialogContent';
import PropTypes from 'prop-types';
import elevations from '../utils/elevations';
import textsStyles from '../utils/textsStyles';
import selectedCardStyles from './selectedCardStyles';
import colors from '../utils/colors';
import dotGrid from '../../images/dotGrid.svg';
import bigTriangle from '../../images/bigTriangle.svg';

const DialogContent = withStyles((theme) => ({
  root: {
    padding: '35px 50px',
    textAlign: 'center',
    position: 'relative',
  },
}))(MuiDialogContent);

const SelectCard = (props) => {
  const {
    setTab,
  } = props;
  const textsClasses = textsStyles();
  const classes = selectedCardStyles();

  const handleContinue = () => {
    setTab(4);
  };
  return (
    <DialogContent className={classes.cardGrid}>
      <Typography className={textsClasses.heading4} style={{ textAlign: 'center' }} component="div">
        <FormattedMessage
          id="schedule.card.paymentMethod"
          defaultMessage="Método de pago"
        />
      </Typography>
      <Typography
        className={textsClasses.body3}
        style={{ textAlign: 'center', color: colors.text02 }}
        component="div"
      >
        <FormattedMessage
          id="schedule.card.selector"
          defaultMessage="Selecciona la tarjeta con la que deseas pagar"
        />
      </Typography>
      <Typography
        component="img"
        src={dotGrid}
        className={classes.dots}
      />
      <Typography
        component="img"
        src={bigTriangle}
        className={classes.triangle}
      />
      <Typography
        component="div"
        className={classes.registerGrid}
        style={{ boxShadow: elevations.e03 }}
      >
        <Typography
          className={textsClasses.body2}
          style={{ textAlign: 'center', color: colors.text02 }}
          component="div"
        >
          <FormattedMessage
            id="schedule.card.noCardRegistered"
            defaultMessage="Parece que aún no tienes registrada ningúna tarjetar"
          />
        </Typography>
        <Button
          variant="contained"
          onClick={handleContinue}
          style={{
            padding: '10px 32px',
            color: colors.textInverse,
            backgroundColor: colors.blueSkyBrand,
            width: '176px',
          }}
          className={textsClasses.heading5}
          color="primary"
        >
          <FormattedMessage
            id="schedule.card.add"
            defaultMessage="Registrar tarjeta"
          />
        </Button>
      </Typography>
    </DialogContent>
  );
};

SelectCard.propTypes = {
  setTab: PropTypes.func.isRequired,
};

export default SelectCard;
