/* eslint-disable react-hooks/exhaustive-deps */
import React, { useState, useEffect } from 'react';
import { useHistory } from 'react-router-dom';
import styled from 'styled-components';
import Media from 'react-media';
import { FormattedMessage } from 'react-intl';
import { Grid } from '@material-ui/core';
import Cover from './Cover';
import Slider from './Slider';
import {
  SaleProvider,
  RecommendationProvider,
  CategoryProvider,
} from '../../services';
import homeStyles from './homeStyles';
import Category from './Category';
import About from './About';
import {
  CARD_WIDTH,
  ARROW_WIDTH,
  MAX_SIZE_SCREEN,
} from '../../constants';
import dotGrid from '../../images/dotGrid.svg';
import middleTriangle from '../../images/middleTriangle.svg';
import mediaQueries from '../utils/mediaQueries';
import GlobalTheme from '../../styled/GlobalTheme';

const DotGrid = styled.img`
  position: absolute;
  width: 170px;
  right: 20px;
  margin-top: -13px;
  ${GlobalTheme.breakpoints.down('md')}{
    left: 20px;
    width: 150px;
  }
  ${GlobalTheme.breakpoints.down('md')}{
    display: none;
  }
`;

const Triangle = styled.img`
  position: absolute;
  left: 170px;
  margin-top: 200px;
  ${GlobalTheme.breakpoints.down('md')}{
    right: 170px;
    left: auto;
  }
  ${GlobalTheme.breakpoints.down('sm')}{
    display: none;
  }
`;

const Header = styled.div`
  font-size: 24px;
  display: flex;
  justify-content: center;
  margin: 15px;
  font-weight: bold;
  ${GlobalTheme.breakpoints.down('sm')}{
    font-size: 20px;
  }
`;

const Home = () => {
  const history = useHistory();
  const classes = homeStyles();
  const [windowWidth] = useState(window.innerWidth);
  const [widthSize, setWidthSize] = useState(MAX_SIZE_SCREEN);
  const [sales, setSales] = useState([]);
  const [rec, setRec] = useState([]);
  const [totalWidth, setTotalWidth] = useState(0);
  const [multiplier, setMultiplier] = useState(2);
  const [smallMultiplier, setSmallMultiplier] = useState(2);
  const [categories, setCategories] = useState([]);
  const getWidthSize = () => {
    if (windowWidth >= MAX_SIZE_SCREEN) {
      return MAX_SIZE_SCREEN;
    }
    return windowWidth;
  };
  const fetchSales = async () => {
    const services = await SaleProvider.fetchAll();
    setSales(services);
  };
  const fetchRec = async () => {
    const services = await RecommendationProvider.fetchAll();
    setRec(services);
  };
  const fetchCategories = async () => {
    const services = await CategoryProvider.fetchAll();
    setCategories(services);
  };

  useEffect(() => {
    setWidthSize(getWidthSize());
    fetchSales();
    fetchRec();
    fetchSales();
    fetchRec();
    fetchCategories();
    getWidthCards();
  }, []);

  const getWidthCards = () => {
    const size = getWidthSize();
    const multiplierCard = parseInt((size * 0.9) / CARD_WIDTH, 10);
    const multiplierSmallCard = multiplierCard * 2;
    setMultiplier(multiplierCard);
    setSmallMultiplier(multiplierSmallCard);
    setTotalWidth((multiplierCard * CARD_WIDTH) + ARROW_WIDTH);
  };

  return (
    <div className={classes.root}>
      <Media queries={mediaQueries}>
        {(matches) => (
          <>
            <Cover />
            <Header>
              <FormattedMessage
                id="cover.subtitle.promotions"
                defaultMessage="Ofertas"
              />
            </Header>
            <Grid
              style={{ display: 'flex', justifyContent: 'center' }}
            >
              <Slider
                items={sales}
                totalWidth={totalWidth}
                multiplier={multiplier}
                widthSize={widthSize}
                cardType="medium"
                history={history}
              />
            </Grid>
            <DotGrid
              src={dotGrid}
            />
            <Header>
              <FormattedMessage
                id="cover.subtitle.recommendation"
                defaultMessage="Recomendaciones"
              />
            </Header>
            <Grid
              style={{ display: 'flex', justifyContent: 'center' }}
            >
              <Triangle
                src={middleTriangle}
              />
              <Slider
                items={rec}
                totalWidth={totalWidth}
                multiplier={smallMultiplier}
                widthSize={widthSize}
                cardType="small"
                history={history}
              />

            </Grid>
            <Header>
              <FormattedMessage
                id="cover.subtitle.explore"
                defaultMessage="Explora"
              />
            </Header>
            <Grid
              style={{ display: 'flex', justifyContent: 'center' }}
            >
              <Category
                items={categories}
                totalWidth={totalWidth}
              />
            </Grid>
            <Grid
              style={{ display: 'flex', justifyContent: 'center' }}
            >
              <About
                totalWidth={totalWidth}
              />
            </Grid>
          </>
        )}
      </Media>
    </div>
  );
};

export default Home;
