import React, { useState, useEffect } from 'react';
import { withStyles } from '@material-ui/core/styles';
import { FormattedMessage } from 'react-intl';
import {
  Typography,
  Button,
} from '@material-ui/core';
import styled from 'styled-components';
import MuiDialogContent from '@material-ui/core/DialogContent';
import PropTypes from 'prop-types';
import textsStyles from '../utils/textsStyles';
import selectedCardStyles from './selectedCardStyles';
import colors from '../utils/colors';
import cardDate from '../../images/cardDate.svg';
import GlobalTheme from '../../styled/GlobalTheme';

const CardImageBox = styled.div`
  position: relative;
  width: 360px;
  height: 207px;
  margin: 20px 0;
  ${GlobalTheme.breakpoints.down('sm')}{
    height: auto;
  }
`;

const CardImage = styled.img`
  ${GlobalTheme.breakpoints.down('sm')}{
    width: 275px;
  }
`;

const NumBox = styled.div`
  text-align: center;
  color: ${({ theme }) => theme.colors.text02};
  font-size: 22px;
  position: absolute;
  top: 83px;
  left: 75px;
  ${GlobalTheme.breakpoints.down('sm')}{
    top: 60px;
    left: 70px;
  }
`;

const NamBox = styled.div`
  text-align: center;
  color: ${({ theme }) => theme.colors.text02};
  fontSize: 13px;
  position: absolute;
  top: 155px;
  left: 28px;
  ${GlobalTheme.breakpoints.down('sm')}{
    top: 110px;
    left: 63px;
  }
`;

const DialogContent = withStyles((theme) => ({
  root: {
    padding: '35px 50px',
    textAlign: 'center',
    position: 'relative',
  },
}))(MuiDialogContent);

const DateCard = (props) => {
  const {
    setTab,
    creditCardInfo,
    setCreditCardInfo,
    addSpace,
  } = props;
  const textsClasses = textsStyles();
  const classes = selectedCardStyles();
  const [isActiveMonth, setIsActiveMonth] = useState(false);
  const [isActiveYear, setIsActiveYear] = useState(false);
  const [name, setName] = useState('');
  const [date, setDate] = useState({
    month: '',
    year: '',
  });
  const [number, setNumber] = useState('xxxxx xxxx xxxx xxxx');
  useEffect(() => {
    setNumber(addSpace(creditCardInfo.number));
    setName(creditCardInfo.name);
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);
  const handleContinue = () => {
    setCreditCardInfo({
      ...creditCardInfo,
      name,
    });
    setTab(7);
  };
  const handleChangeMonth = (event) => {
    const { value } = event.target;
    if (/^(\s*|\d+)$/.test(value) && value.length <= 2) {
      setDate({
        ...date,
        month: value,
      });
    }
    setIsActiveMonth(value !== '');
  };
  const handleChangeYear = (event) => {
    const { value } = event.target;
    if (/^(\s*|\d+)$/.test(value) && value.length <= 2) {
      setDate({
        ...date,
        year: value,
      });
    }
    setIsActiveYear(value !== '');
  };
  return (
    <DialogContent className={classes.cardGrid}>
      <Typography className={textsClasses.heading4} style={{ textAlign: 'center' }} component="div">
        <FormattedMessage
          id="schedule.card.register"
          defaultMessage="Registrar Tarjeta"
        />
      </Typography>
      <Typography
        className={textsClasses.body3}
        style={{ textAlign: 'center', color: colors.text02 }}
        component="div"
      >
        <FormattedMessage
          id="schedule.card.details"
          defaultMessage="Introduce los detalles de tu tarjeta"
        />
      </Typography>
      <CardImageBox>
        <NumBox>
          {number}
        </NumBox>
        <NamBox>
          {name}
        </NamBox>
        <CardImage
          alt="Card date"
          src={cardDate}
        />
      </CardImageBox>
      <Typography
        component="div"
        style={{ width: '300px' }}
      >
        <Typography
          className={textsClasses.body5}
          component="div"
        >
          <FormattedMessage
            id="schedule.card.expirationDate"
            defaultMessage="Fecha de vencimiento"
          />
        </Typography>
        <Typography
          component="div"
          style={{ display: 'flex', justifyContent: 'center' }}
        >
          <input
            type="text"
            name="month"
            onChange={handleChangeMonth}
            placeholder="MM"
            className={`${classes.inputDate} ${textsClasses.body5}`}
            value={date.month}
          />
          <Typography
            className={`${textsClasses.body5} ${classes.slash}`}
            component="div"
          >
            /
          </Typography>
          <input
            type="text"
            name="year"
            onChange={handleChangeYear}
            placeholder="YY"
            className={`${classes.inputDate} ${textsClasses.body5}`}
            value={date.year}
          />
        </Typography>
      </Typography>
      <Button
        variant="contained"
        onClick={handleContinue}
        style={{
          margin: '35px 0px',
          padding: '10px 32px',
          color: colors.textInverse,
          backgroundColor: (
            isActiveYear && isActiveMonth
          )
            ? colors.blueSkyBrand
            : colors.noSelectedBSB,
        }}
        className={textsClasses.heading5}
        color="primary"
        disabled={!(isActiveYear && isActiveMonth)}
      >
        <FormattedMessage
          id="global.continue"
          defaultMessage="Continuar"
        />
      </Button>
    </DialogContent>
  );
};

DateCard.propTypes = {
  setTab: PropTypes.func.isRequired,
  creditCardInfo: PropTypes.instanceOf(Object).isRequired,
  setCreditCardInfo: PropTypes.func.isRequired,
  addSpace: PropTypes.func.isRequired,
};

export default DateCard;
