import React, { useState, useEffect } from 'react';
import { FormattedMessage } from 'react-intl';
import styled from 'styled-components';
import { Typography } from '@material-ui/core';
import { INFORMATION_FOOTER, SERVICE_FOOTER } from '../constants';
import { CategoryProvider } from '../services';
import GlobalTheme from '../styled/GlobalTheme';

const Container = styled.div`
  display: flex;
  justify-content: center;
  background: ${({ theme }) => (theme.colors.whiteBlue)};
  width: 100%;
`;

const Wrapper = styled.div`
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  margin: 50px 0;
  text-align: left;
  width: 980px;
  ${GlobalTheme.breakpoints.down('md')}{
    width: 100%;
    margin: 25px 50px;
  }
`;

const Column = styled.div``;

const Title = styled.p`
  color: ${({ theme }) => (theme.colors.text02)};
  font-weight: bold;
  font-size: 18px;
  line-height: 24px;
  ${GlobalTheme.breakpoints.down('md')}{
    font-size: 16px;
    line-height: 22px;
  }
`;

const Text = styled.p`
  color: ${({ theme }) => (theme.colors.ui05)};
  font-size: 14px;
  line-height: 24px;
  ${GlobalTheme.breakpoints.down('md')}{
    font-size: 12px;
    line-height: 22px;
  }
`;

const CategoryColumn = styled.div`
  ${GlobalTheme.breakpoints.down('sm')}{
    display: none;
  }
`;

const Categories = styled.div`
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  width: 350px;
  ${GlobalTheme.breakpoints.down('md')}{
    width: 250px;
  }
`;

const Footer = () => {
  const [categories, setCategories] = useState([]);
  const fetchCategories = async () => {
    const services = await CategoryProvider.fetchAll();
    setCategories(services);
  };
  useEffect(() => {
    fetchCategories();
  }, []);

  const getColumnCategory = (column, items) => {
    if (column === 1) {
      return items.slice(0, 3);
    }
    if (column === 2) {
      return items.slice(3, 6);
    }
    if (column === 3) {
      return items.slice(6, items.length);
    }
    return items.slice(0, 3);
  };
  return (
    <Container>
      <Wrapper>
        <Column>
          <Title>
            <FormattedMessage
              id="footer.title1"
              defaultMessage="Información"
            />
          </Title>
          {INFORMATION_FOOTER.map((item) => (
            <Text key={item.id}>
              <FormattedMessage
                id={item.id}
                defaultMessage={item.name}
              />
            </Text>
          ))}
        </Column>
        <CategoryColumn>
          <Title>
            <FormattedMessage
              id="footer.title2"
              defaultMessage="Categrías"
            />
          </Title>
          <Categories>
            <Typography
              component="div"
            >
              {getColumnCategory(1, categories).map((item) => (
                <Text
                  key={item.id}
                >
                  {item.name}
                </Text>
              ))}
            </Typography>
            <Typography
              component="div"
            >
              {getColumnCategory(2, categories).map((item) => (
                <Text
                  key={item.id}
                >
                  {item.name}
                </Text>
              ))}
            </Typography>
            <Typography
              component="div"
            >
              {getColumnCategory(3, categories).map((item) => (
                <Text
                  key={item.id}
                >
                  {item.name}
                </Text>
              ))}
            </Typography>
          </Categories>
        </CategoryColumn>
        <Column>
          <Title>
            <FormattedMessage
              id="footer.title3"
              defaultMessage="Servicio"
            />
          </Title>
          {SERVICE_FOOTER.map((item) => (
            <Text
              key={item.id}
            >
              <FormattedMessage
                id={item.id}
                defaultMessage={item.name}
              />
            </Text>
          ))}
        </Column>
      </Wrapper>
    </Container>
  );
};

export default Footer;
