import React, { useState, useEffect } from 'react';
import { withStyles } from '@material-ui/core/styles';
import {
  Dialog,
  IconButton,
  Typography,
} from '@material-ui/core';
import styled from 'styled-components';
import { FormattedMessage } from 'react-intl';
import MuiDialogTitle from '@material-ui/core/DialogTitle';
import MuiDialogContent from '@material-ui/core/DialogContent';
import ArrowBackOutlinedIcon from '@material-ui/icons/ArrowBackOutlined';
import PropTypes from 'prop-types';
import serviceStyles from './serviceStyles';
import textsStyles from '../utils/textsStyles';
import colors from '../utils/colors';
import dotGrid from '../../images/dotGrid.svg';
import bigCircle from '../../images/bigCircle.svg';
import GlobalTheme from '../../styled/GlobalTheme';

const DialogStyled = styled(Dialog)`
  && {
    .MuiPaper-root{
      ${GlobalTheme.breakpoints.down('sm')}{
        width: 100%;
        margin: 0;
        max-height: 100%;
        border-radius: 0;
      } 
    }
  }
`;

const styles = (theme) => ({
  closeButton: {
    position: 'absolute',
    left: theme.spacing(1),
    top: theme.spacing(1),
  },
});

const DialogTitle = withStyles(styles)((props) => {
  const {
    classes,
    onClose,
  } = props;
  return (
    <MuiDialogTitle style={{ height: '15px' }}>
      {onClose ? (
        <IconButton aria-label="close" className={classes.closeButton} onClick={onClose}>
          <ArrowBackOutlinedIcon />
        </IconButton>
      ) : null}
    </MuiDialogTitle>
  );
});

const DialogContent = withStyles((theme) => ({
  root: {
    padding: '50px',
  },
}))(MuiDialogContent);

const Profile = (props) => {
  const {
    isOpen,
    setIsOpen,
    profile,
  } = props;
  const textsClasses = textsStyles();
  const classes = serviceStyles();
  const [educationData, setEducationData] = useState([]);
  const [experienceData, setExperienceData] = useState([]);
  useEffect(() => {
    if (profile.education !== undefined) {
      setEducationData(profile.education);
    }
    if (profile.experience !== undefined) {
      setExperienceData(profile.experience);
    }
  }, [profile]);
  const handleClose = () => {
    setIsOpen(false);
  };

  return (
    <DialogStyled
      onClose={handleClose}
      aria-labelledby="customized-dialog-title"
      open={isOpen}
    >
      <DialogTitle id="customized-dialog-title" onClose={handleClose} />
      <DialogContent>
        <Typography
          component="div"
          style={{
            textAlign: 'center',
            position: 'relative',
            height: '250px',
          }}
        >
          <Typography
            component="img"
            src={bigCircle}
            className={classes.circle}
          />
          <Typography
            component="img"
            src={dotGrid}
            className={classes.dots}
          />
          <Typography
            component="img"
            className={classes.profileImage}
            style={{ borderRadius: '8px' }}
            src={profile.url}
          />
        </Typography>
        <Typography
          component="div"
        >
          <Typography className={textsClasses.heading4} component="div">
            <FormattedMessage
              id="global.about"
              defaultMessage="Acerca de"
            />
            {' '}
            {profile.fullName}
          </Typography>
          <br />
          <Typography
            className={textsClasses.body3}
            style={{ color: colors.text02 }}
            component="div"
          >
            {profile.description}
          </Typography>
          <br />
        </Typography>
        <Typography
          component="div"
        >
          <Typography className={textsClasses.heading4} component="div">
            <FormattedMessage
              id="global.experience"
              defaultMessage="Experiencia"
            />
          </Typography>
          <br />
          {experienceData.map((experience) => (
            <Typography
              key={experience.id}
              className={classes.flex}
              style={{ marginBottom: '24px' }}
              component="div"
            >
              <Typography
                component="img"
                style={{ height: '54px', marginRight: '15px' }}
                src={experience.url}
              />
              <Typography
                component="div"
              >
                <Typography
                  className={textsClasses.body3}
                  style={{ color: colors.text02 }}
                  component="div"
                >
                  {experience.title}
                </Typography>
                <Typography
                  className={textsClasses.body3}
                  style={{ color: colors.text02 }}
                  component="div"
                >
                  {experience.institution}
                </Typography>
                <Typography
                  className={textsClasses.body2}
                  style={{ color: colors.text03 }}
                  component="div"
                >
                  {experience.startDate}
                  {' '}
                  -
                  {' '}
                  {experience.endDate}
                </Typography>
              </Typography>
            </Typography>
          ))}
        </Typography>
        <Typography className={classes.grayLine} component="div" />
        <Typography
          component="div"
        >
          <Typography className={textsClasses.heading4} component="div">
            <FormattedMessage
              id="global.education"
              defaultMessage="Educación"
            />
          </Typography>
          <br />
          {educationData.map((education) => (
            <Typography
              key={education.id}
              className={classes.flex}
              style={{ marginBottom: '24px' }}
              component="div"
            >
              <Typography
                component="img"
                style={{ height: '54px', marginRight: '15px' }}
                src={education.url}
              />
              <Typography
                component="div"
              >
                <Typography
                  className={textsClasses.body3}
                  style={{ color: colors.text02 }}
                  component="div"
                >
                  {education.title}
                </Typography>
                <Typography
                  className={textsClasses.body3}
                  style={{ color: colors.text02 }}
                  component="div"
                >
                  {education.institution}
                </Typography>
                <Typography
                  className={textsClasses.body2}
                  style={{ color: colors.text03 }}
                  component="div"
                >
                  {education.startDate}
                  {' '}
                  -
                  {' '}
                  {education.endDate}
                </Typography>
              </Typography>
            </Typography>
          ))}
        </Typography>
      </DialogContent>
    </DialogStyled>
  );
};

Profile.propTypes = {
  isOpen: PropTypes.bool.isRequired,
  setIsOpen: PropTypes.func.isRequired,
  profile: PropTypes.instanceOf(Object).isRequired,
};

export default Profile;
