import React, { useState, useEffect } from 'react';
import { withStyles } from '@material-ui/core/styles';
import {
  Typography,
  Button,
} from '@material-ui/core';
import styled from 'styled-components';
import { FormattedMessage, useIntl } from 'react-intl';
import MuiDialogContent from '@material-ui/core/DialogContent';
import PropTypes from 'prop-types';
import textsStyles from '../utils/textsStyles';
import selectedCardStyles from './selectedCardStyles';
import colors from '../utils/colors';
import cardName from '../../images/cardName.svg';
import GlobalTheme from '../../styled/GlobalTheme';

const CardImageBox = styled.div`
  position: relative;
  width: 360px;
  height: 207px;
  margin: 20px 0;
  ${GlobalTheme.breakpoints.down('sm')}{
    height: auto;
  }
`;

const CardImage = styled.img`
  ${GlobalTheme.breakpoints.down('sm')}{
    width: 275px;
  }
`;

const InputBox = styled.div`
  text-align: left;
  width: 300px;
  ${GlobalTheme.breakpoints.down('sm')}{
    width: 100%;
  }
`;

const Input = styled.input`
  padding: 16px;
  color: ${({ theme }) => (theme.colors.text03)};
  border: 1px solid ${({ theme }) => (theme.colors.ui05)};
  border-radius: 4px;
  font-size: 12px;
  width: 300px;
  ${GlobalTheme.breakpoints.down('sm')}{
    width: 240px;
  }
`;

const NumBox = styled.div`
  text-align: center;
  color: ${({ theme }) => theme.colors.text02};
  font-size: 22px;
  position: absolute;
  top: 83px;
  left: 75px;
  ${GlobalTheme.breakpoints.down('sm')}{
    top: 60px;
    left: 70px;
  }
`;

const DialogContent = withStyles((theme) => ({
  root: {
    padding: '35px 50px',
    textAlign: 'center',
    position: 'relative',
  },
}))(MuiDialogContent);

const NameCard = (props) => {
  const {
    setTab,
    creditCardInfo,
    setCreditCardInfo,
    addSpace,
  } = props;
  const textsClasses = textsStyles();
  const classes = selectedCardStyles();
  const intl = useIntl();
  const [isActive, setIsActive] = useState(false);
  const [name, setName] = useState('');
  const [number, setNumber] = useState('xxxxx xxxx xxxx xxxx');
  useEffect(() => {
    setNumber(addSpace(creditCardInfo.number));
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);
  const handleContinue = () => {
    setCreditCardInfo({
      ...creditCardInfo,
      name,
    });
    setTab(6);
  };
  const handleChangeName = (event) => {
    const { value } = event.target;
    setIsActive(value !== '');
    setName(value);
  };

  return (
    <DialogContent className={classes.cardGrid}>
      <Typography className={textsClasses.heading4} style={{ textAlign: 'center' }} component="div">
        <FormattedMessage
          id="schedule.card.register"
          defaultMessage="Registrar Tarjeta"
        />
      </Typography>
      <Typography
        className={textsClasses.body3}
        style={{ textAlign: 'center', color: colors.text02 }}
        component="div"
      >
        <FormattedMessage
          id="schedule.card.details"
          defaultMessage="Introduce los detalles de tu tarjeta"
        />
      </Typography>
      <CardImageBox>
        <NumBox>
          {number}
        </NumBox>
        <CardImage
          alt="Card Name"
          src={cardName}
        />
      </CardImageBox>
      <InputBox>
        <Typography
          className={textsClasses.body5}
          component="div"
        >
          <FormattedMessage
            id="schedule.card.name"
            defaultMessage="Nombre"
          />
        </Typography>
        <Input
          type="text"
          name="name"
          onChange={handleChangeName}
          placeholder={intl.formatMessage({
            id: 'schedule.card.name.inpu',
            defaultMessage: 'Escribe tu nombre',
          })}
          value={name}
        />
      </InputBox>
      <Button
        variant="contained"
        onClick={handleContinue}
        style={{
          margin: '35px 0px',
          padding: '10px 32px',
          color: colors.textInverse,
          backgroundColor: (
            isActive
          )
            ? colors.blueSkyBrand
            : colors.noSelectedBSB,
        }}
        className={textsClasses.heading5}
        color="primary"
        disabled={!isActive}
      >
        <FormattedMessage
          id="global.continue"
          defaultMessage="Continuar"
        />
      </Button>
    </DialogContent>
  );
};

NameCard.propTypes = {
  setTab: PropTypes.func.isRequired,
  creditCardInfo: PropTypes.instanceOf(Object).isRequired,
  setCreditCardInfo: PropTypes.func.isRequired,
  addSpace: PropTypes.func.isRequired,
};

export default NameCard;
