/* eslint-disable react/no-array-index-key */
import React, { useState } from 'react';
import { withStyles } from '@material-ui/core/styles';
import { FormattedMessage } from 'react-intl';
import {
  Typography,
  Button,
} from '@material-ui/core';

import MuiDialogContent from '@material-ui/core/DialogContent';
import styled from 'styled-components';
import PropTypes from 'prop-types';
import Calendar from './Calendar';
import Hours from './Hours';
import scheduleStyles from './scheduleStyles';
import textsStyles from '../utils/textsStyles';
import colors from '../utils/colors';
import dotGrid from '../../images/dotGrid.svg';
import bigCircle from '../../images/bigCircle.svg';
import GlobalTheme from '../../styled/GlobalTheme';


const ScheduleContainer = styled.div`
  display: flex;
  justify-content: center;
  ${GlobalTheme.breakpoints.down('sm')}{
    flex-wrap: wrap;
  }
`;

const DialogContent = withStyles((theme) => ({
  root: {
    padding: '35px 50px',
    textAlign: 'center',
    position: 'relative',
  },
}))(MuiDialogContent);

const Schedule = (props) => {
  const {
    schedule,
    setTab,
  } = props;
  const textsClasses = textsStyles();
  const classes = scheduleStyles();
  const [disableBtn] = useState(true);
  const [hours, setHours] = useState([]);
  const [activeIndexHour, setActiveIndexHour] = useState(-1);
  const [activeIndexDay, setActiveIndexDay] = useState(-1);

  const handleContinue = () => {
    setTab(2);
    // setActiveIndexHour(-1);
    // setActiveIndexDay(-1);
    // setHours([]);
  };
  return (
    <DialogContent>
      <Typography
        component="img"
        src={bigCircle}
        className={classes.circle}
      />
      <Typography
        component="img"
        src={dotGrid}
        className={classes.dots}
      />
      <Typography className={textsClasses.heading4} style={{ textAlign: 'center' }} component="div">
        <FormattedMessage
          id="schedule.selector"
          defaultMessage="Selecciona un horario"
        />
      </Typography>
      <ScheduleContainer>
        <Typography className={classes.calendar} component="div">
          <Typography className={textsClasses.heading5} style={{ textAlign: 'center', margin: '36px 0 20px 0' }} component="div">
            <FormattedMessage
              id="schedule.day"
              defaultMessage="Día"
            />
          </Typography>
          <Typography style={{ display: 'flex', justifyContent: 'center' }} component="div">
            <Calendar
              schedule={schedule}
              setHours={setHours}
              setActiveIndexHour={setActiveIndexHour}
              activeIndexDay={activeIndexDay}
              setActiveIndexDay={setActiveIndexDay}
            />
          </Typography>
        </Typography>
        {hours.length > 0 && (
          <Hours
            hours={hours}
            setActiveIndexHour={setActiveIndexHour}
            activeIndexHour={activeIndexHour}
          />
        )}
      </ScheduleContainer>
      <Button
        variant="contained"
        onClick={handleContinue}
        style={{
          margin: '35px 0px',
          padding: '10px 32px',
          color: colors.textInverse,
          backgroundColor: (
            activeIndexDay >= 0
            && activeIndexHour >= 0
          )
            ? colors.blueSkyBrand
            : colors.noSelectedBSB,
        }}
        className={textsClasses.heading5}
        color="primary"
        disabled={(activeIndexDay >= 0 && activeIndexHour >= 0) ? !disableBtn : disableBtn}
      >
        <FormattedMessage
          id="global.continue"
          defaultMessage="Continuar"
        />
      </Button>
    </DialogContent>
  );
};

Schedule.propTypes = {
  schedule: PropTypes.instanceOf(Array).isRequired,
  setTab: PropTypes.func.isRequired,
};

export default Schedule;
