import React, { useState } from 'react';
import PropTypes from 'prop-types';
import styled from 'styled-components';
import {
  Grid,
  Typography,
  Button,
  IconButton,
} from '@material-ui/core';
import { FormattedMessage } from 'react-intl';
import ShareOutlinedIcon from '@material-ui/icons/ShareOutlined';
import BookmarkBorderIcon from '@material-ui/icons/BookmarkBorder';
import BookmarkOutlinedIcon from '@material-ui/icons/BookmarkOutlined';
import { CopyToClipboard } from 'react-copy-to-clipboard';
import serviceStyles from './serviceStyles';
import textsStyles from '../utils/textsStyles';
import colors from '../utils/colors';
import GlobalTheme from '../../styled/GlobalTheme';

const Box = styled.div`
  display: flex;
  justify-content: space-between;
  ${GlobalTheme.breakpoints.down('sm')}{
    flex-wrap: wrap;
  }
`;

const Description = (props) => {
  const {
    item,
    windowWidth,
    ratingStars,
    profile,
    setProfileIsOpen,
    setScheduleIsOpen,
  } = props;
  const currentUrl = window.location.href;
  const textsClasses = textsStyles();
  const classes = serviceStyles();
  const [hasBookMark, setHasBookMark] = useState(false);
  const addBookMark = () => {
    setHasBookMark(!hasBookMark);
  };
  const handleClickProfileOpen = () => {
    setProfileIsOpen(true);
  };
  const handleClickScheduleOpen = () => {
    setScheduleIsOpen(true);
  };
  return (
    <Grid item className={classes.flex} style={{ flexWrap: 'wrap', paddingTop: '0' }} xs={12}>
      { windowWidth > 1260 && (
        <Typography component="div" className={classes.imageSize}>
          <Typography component="img" src={item.url} className={classes.imageSize} />
        </Typography>
      )}
      <Typography className={classes.serviceCard} style={{ minWidth: '250px', maxWidth: '580px' }} component="div">
        <Typography
          className={`${classes.flex} ${classes.space}`}
          component="div"
        >
          <Typography className={textsClasses.heading3} component="div">
            {item.name}
          </Typography>
          <Typography component="div">
            <CopyToClipboard text={currentUrl}>
              <IconButton
                edge="start"
                className={classes.spacing}
                color="inherit"
                aria-label="share"
                disableFocusRipple
                disableRipple
              >
                <ShareOutlinedIcon />
              </IconButton>
            </CopyToClipboard>
            <IconButton
              onClick={addBookMark}
              edge="start"
              color="inherit"
              aria-label="bookmark"
              disableFocusRipple
              disableRipple
            >
              { !hasBookMark && (
                <BookmarkBorderIcon />
              )}
              { hasBookMark && (
                <BookmarkOutlinedIcon />
              )}
            </IconButton>
          </Typography>
        </Typography>
        <Typography
          className={textsClasses.heading5}
          style={{ color: colors.text03 }}
          component="div"
        >
          {profile.fullName}
          ,
          {profile.lastTitle}
        </Typography>
        <br />
        <Typography component="div" className={classes.flex}>
          <Button
            variant="outlined"
            className={classes.marginButtons}
            color="secondary"
            onClick={handleClickProfileOpen}
          >
            <FormattedMessage
              id="service.about.instructor"
              defaultMessage="Acerca del Instructor"
            />
          </Button>
          <Button
            variant="contained"
            onClick={handleClickScheduleOpen}
            color="secondary"
          >
            <FormattedMessage
              id="service.schedule"
              defaultMessage="Agendar"
            />
          </Button>
        </Typography>
        <br />
        <Typography className={textsClasses.heading4} component="div">
          <FormattedMessage
            id="service.about"
            defaultMessage="Acerca del curso"
          />
        </Typography>
        <Typography
          className={textsClasses.body3}
          component="div"
          style={{ color: colors.text02 }}
        >
          {profile.description}
        </Typography>
        <br />
        <Box className={`${classes.flex} ${classes.space}`} component="div">
          <Typography component="div">
            <Typography
              className={textsClasses.heading4}
              style={{ paddingBottom: '10px' }}
              component="div"
            >
              <FormattedMessage
                id="service.rating"
                defaultMessage="Valoración"
              />
            </Typography>
            {ratingStars(item.rating)}
          </Typography>
          <Typography component="div">
            <Typography
              className={textsClasses.heading4}
              component="div"
              style={{ paddingBottom: '10px' }}
            >
              <FormattedMessage
                id="service.price"
                defaultMessage="Costo"
              />
            </Typography>
            <Typography
              className={`${textsClasses.body3} ${item.hasSale ? classes.salePrice : classes.price}`}
              component="div"
            >
              $
              {item.price}
              {' '}
              MXN
              {' '}
              <FormattedMessage
                id="service.bySession"
                defaultMessage="por sesión"
              />
            </Typography>
            {item.hasSale && (
              <Typography
                className={textsClasses.body5}
                style={{ margin: '0.5rem 0', color: colors.text02 }}
                component="div"
              >
                <FormattedMessage
                  id="service.original"
                  defaultMessage="Original"
                />
                : $
                {item.beforeSalePrice}
                {' '}
                MXN
                {' '}
                <FormattedMessage
                  id="service.bySession"
                  defaultMessage="por sesión"
                />
              </Typography>
            )}
          </Typography>
          <Typography component="div">
            <Typography
              className={textsClasses.heading4}
              component="div"
              style={{ paddingBottom: '10px' }}
            >
              <FormattedMessage
                id="service.duration"
                defaultMessage="Duración"
              />
            </Typography>
            <Typography
              className={textsClasses.body3}
              component="div"
              style={{ color: colors.text02 }}
            >
              {item.duration}
              {' '}
              <FormattedMessage
                id="schedule.hour"
                defaultMessage="Hora"
              />
            </Typography>
          </Typography>
        </Box>
      </Typography>
    </Grid>
  );
};

Description.propTypes = {
  item: PropTypes.instanceOf(Object).isRequired,
  windowWidth: PropTypes.number.isRequired,
  ratingStars: PropTypes.func.isRequired,
  profile: PropTypes.instanceOf(Object).isRequired,
  setProfileIsOpen: PropTypes.func.isRequired,
  setScheduleIsOpen: PropTypes.func.isRequired,
};

export default Description;
