import React from 'react';
import PropTypes from 'prop-types';
import styled from 'styled-components';
import { SMALL_CARD } from '../../constants';
import GlobalTheme from '../../styled/GlobalTheme';

const Container = styled.div`
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  margin-bottom: 100px;
  cursor: pointer;
  width: ${({ totalWidth }) => (totalWidth)}px;
  ${GlobalTheme.breakpoints.down('md')}{
    width: 100%;
  }
  ${GlobalTheme.breakpoints.down('sm')}{
  }
`;

const IconGrid = styled.div`
  width: ${SMALL_CARD};
  height: 200px,
  border-radius: 8px;
  text-align: left;
  margin: 15px;
  ${GlobalTheme.breakpoints.down('md')}{
    width: 150px;
    height: 125px;
  }
  ${GlobalTheme.breakpoints.down('sm')}{
    width: 140px;
    height: 116px;
  }
`;

const IconCard = styled.div`
  width: ${SMALL_CARD};
  height: 140px;
  display: flex;
  border-radius: 8px;
  box-shadow: 0px 6px 12px rgba(0, 0, 0, 0.1);
  padding: 30px 0;
  flex-direction: column;
  justify-content: space-between;
  ${GlobalTheme.breakpoints.down('md')}{
    width: 150px;
    height: 88px;
  }
  ${GlobalTheme.breakpoints.down('sm')}{
    width: 140px;
    height: 82px;
  }
`;

const Title = styled.div`
  font-weight: bold;
  font-size: 20px;
  text-align: center;
  ${GlobalTheme.breakpoints.down('md')}{
    font-size: 18px;
  }
  ${GlobalTheme.breakpoints.down('sm')}{
    font-size: 16px;
  }
`;

const Icon = styled.img`
  ${GlobalTheme.breakpoints.down('md')}{
    width: 150px;
    height: 60px;
  }
`;

const Category = (props) => {
  const {
    items,
    totalWidth,
  } = props;
  return (
    <Container totalWidth={totalWidth}>
      {items.map((item) => (
        <IconGrid key={item.id}>
          <IconCard title="icon">
            <Title align="center">
              {item.name}
            </Title>
            <Icon src={item.url} />
          </IconCard>
        </IconGrid>
      ))}
    </Container>
  );
};

Category.propTypes = {
  items: PropTypes.instanceOf(Array).isRequired,
  totalWidth: PropTypes.number.isRequired,
};

export default Category;
