import React, { useEffect, useState, useRef } from 'react';
import PropTypes from 'prop-types';
import { IntlProvider as ReactIntlProvider } from 'react-intl';
import '@formatjs/intl-pluralrules/polyfill';
import '@formatjs/intl-relativetimeformat/polyfill';
import '@formatjs/intl-pluralrules/locale-data/en';
import '@formatjs/intl-relativetimeformat/locale-data/en';
import '@formatjs/intl-pluralrules/locale-data/es';
import '@formatjs/intl-relativetimeformat/locale-data/es';
import translations from '../i18n/locales';
import useDidUpdateEffect from '../hooks/useDidUpdateEffect';
// import removeLanguageRegion from '../utils/removeLanguageRegion';

const LanguageContext = React.createContext();
const LanguageProvider = ({ children }) => {
  const didLoadRef = useRef(false);
  const [state, setState] = useState({
    locale: 'en',
    messages: null,
  });
  const [language, setLanguage] = useState('en');
  const [onboardingDetails, setOnboardingDetails] = useState({
    finished: false,
    nextStep: 1,
    requiredCard: false,
  });
  const handleLanguageChange = (lan = language) => {
    // Split languages with a region code
    // en-US
    // en-GB
    // [IMPORTANT] uncomment to get the language according to the browser
    // const languageWithoutRegionCode = removeLanguageRegion(lan);
    const languageWithoutRegionCode = 'es';
    // Try full language, fallback to language without region code
    if (translations[lan]) {
      setState({
        locale: lan,
        messages: translations[lan],
      });
    } else if (translations[languageWithoutRegionCode]) {
      setState({
        locale: languageWithoutRegionCode,
        messages: translations[languageWithoutRegionCode],
      });
    } else {
      // Fallback en
      setState({
        locale: 'en',
        messages: translations.en,
      });
    }
  };
  useDidUpdateEffect(() => {
    handleLanguageChange();
  }, [language]);

  const fetchLanguage = async () => {
    didLoadRef.current = true;
    const lan = (navigator.languages && navigator.languages[0])
    || navigator.language
    || navigator.userLanguage;
    setLanguage(lan);
    if (lan === language && lan !== state.locale) {
      handleLanguageChange(lan);
    }
  };

  useEffect(() => {
    fetchLanguage();
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);


  return (
    <LanguageContext.Provider
      value={{
        locale: state.locale,
        setLanguage,
        messages: state.messages,
        handleLanguageChange,
        fetchLanguage,
        onboardingDetails,
        setOnboardingDetails,
      }}
    >
      <ReactIntlProvider
        locale={state.locale}
        defaultLocale="en"
        messages={state.messages}
      >
        {children}
      </ReactIntlProvider>
    </LanguageContext.Provider>
  );
};
LanguageProvider.propTypes = {
  children: PropTypes.node.isRequired,
};
export { LanguageProvider, LanguageContext };
