import React, { useState } from 'react';
import PropTypes from 'prop-types';
import Media from 'react-media';
import { Typography } from '@material-ui/core';
import ArrowBackIosIcon from '@material-ui/icons/ArrowBackIos';
import ArrowForwardIosIcon from '@material-ui/icons/ArrowForwardIos';
import homeStyles from './homeStyles';
import MediumCard from './MediumCard';
import SmallCard from './SmallCard';
import {
  CARD_WIDTH,
  RIGTH_TYPE,
  LEFT_TYPE,
  SMALL_CARD_WIDTH,
  SLIDER_TRANS,
} from '../../constants';
import mediaQueries from '../utils/mediaQueries';
import SliderMobile from './SliderMobile';

const Slider = (props) => {
  const {
    items,
    totalWidth,
    multiplier,
    widthSize,
    cardType,
    history,
  } = props;
  const cardWidth = cardType === 'small' ? SMALL_CARD_WIDTH : CARD_WIDTH;
  const classes = homeStyles();
  const [count, setCount] = useState(0);
  const [effect, setEffect] = useState({
    activeIndex: 0,
    translate: 0,
    transition: SLIDER_TRANS,
    type: RIGTH_TYPE,
    displayNext: true,
    displayPrev: false,
  });
  const prevSlide = () => {
    const { activeIndex } = effect;
    let counter = count;
    setCount(counter -= 1);
    if (effect.activeIndex === 0) {
      return setEffect({
        ...effect,
        activeIndex,
        type: RIGTH_TYPE,
      });
    }
    setEffect({
      ...effect,
      activeIndex: activeIndex + multiplier,
      translate: (activeIndex + multiplier) * cardWidth,
      type: RIGTH_TYPE,
      displayNext: !(((items.length / multiplier) - 1) === counter),
      displayPrev: !(counter === 0),
    });
    return true;
  };
  const nextSlide = () => {
    const { activeIndex } = effect;
    if (effect.activeIndex === -(items.length - multiplier)) {
      return setEffect({
        ...effect,
        activeIndex,
        type: LEFT_TYPE,
      });
    }
    let counter = count;
    setCount(counter += 1);
    setEffect({
      ...effect,
      activeIndex: activeIndex - multiplier,
      translate: ((activeIndex - multiplier) * -(cardWidth)),
      type: LEFT_TYPE,
      displayNext: !(((items.length / multiplier) - 1) === counter),
      displayPrev: !(counter === 0),
    });
    return true;
  };
  const getWidth = (totalItems) => widthSize * totalItems;
  return (
    <Media queries={mediaQueries}>
      {(matches) => (
        <>
          {matches.mdDown ? (
            <SliderMobile cardType={cardType}>
              {items.map((item) => (
                cardType === 'medium' ? (
                  <MediumCard
                    item={item}
                    effect={effect}
                    history={history}
                    key={item.id}
                  />
                ) : (
                  <SmallCard
                    item={item}
                    effect={effect}
                    history={history}
                    key={item.id}
                  />
                )
              ))}
            </SliderMobile>
          ) : (
            <Typography
              component="div"
              className={classes.slider}
              id={cardType}
              style={{ width: `${totalWidth}px` }}
            >
              <Typography
                className={classes.btnContainer}
                onClick={prevSlide}
                size="small"
              >
                {effect.displayPrev && (
                <ArrowBackIosIcon fontSize="inherit" />
                )}
              </Typography>
              <Typography component="div" className={classes.SliderContentViewer}>
                <Typography
                  component="div"
                  className={classes.SliderContent}
                  style={{
                    width: getWidth(items.length),
                  }}
                >
                  {items.map((item) => (
                    cardType === 'medium' ? (
                      <MediumCard
                        item={item}
                        effect={effect}
                        history={history}
                        key={item.id}
                      />
                    ) : (
                      <SmallCard
                        item={item}
                        effect={effect}
                        history={history}
                        key={item.id}
                      />
                    )
                  ))}
                </Typography>
              </Typography>
              <Typography
                className={classes.btnContainer}
                onClick={nextSlide}
                size="small"
              >
                {effect.displayNext && (
                <ArrowForwardIosIcon fontSize="inherit" />
                )}
              </Typography>
            </Typography>
          )}
        </>
      )}
    </Media>
  );
};

Slider.propTypes = {
  items: PropTypes.instanceOf(Array).isRequired,
  totalWidth: PropTypes.number.isRequired,
  multiplier: PropTypes.number.isRequired,
  widthSize: PropTypes.number.isRequired,
  cardType: PropTypes.string.isRequired,
  history: PropTypes.shape({
    push: PropTypes.func.isRequired,
  }).isRequired,
};

export default Slider;
