/* eslint-disable arrow-body-style */
/* eslint-disable react/no-array-index-key */
import React, { useState, useEffect } from 'react';
import { useHistory, useParams } from 'react-router-dom';
import {
  Paper,
  Grid,
  Typography,
} from '@material-ui/core';
import styled from 'styled-components';
import { FormattedMessage } from 'react-intl';
import ErrorIcon from '@material-ui/icons/Error';
import Description from './Description';
import Comments from './Comments';
import Profile from './Profile';
import Handler from '../schedule';
import {
  RecommendationProvider,
  ServiceProvider,
} from '../../services';
import serviceStyles from './serviceStyles';
import textsStyles from '../utils/textsStyles';
import star01 from '../../images/star01.svg';
import star02 from '../../images/star02.svg';
import star03 from '../../images/star03.svg';
import {
  TOTAL_RATING,
  CARD_WIDTH,
  ARROW_WIDTH,
  MAX_SIZE_SCREEN,
} from '../../constants';
import Slider from '../home/Slider';
import GlobalTheme from '../../styled/GlobalTheme';

const Container = styled.div`
  flex-grow: 1;
  margin-top: 64px;
  padding: 2rem 130px;
  ${GlobalTheme.breakpoints.down('md')}{
    padding: 2rem 60px;
  }
  ${GlobalTheme.breakpoints.down('sm')}{
    padding: 2rem 20px;
  }
`;

const Service = () => {
  const { id } = useParams();
  const windowWidth = window.innerWidth;
  const history = useHistory();
  const getWidthSize = () => {
    if (windowWidth >= MAX_SIZE_SCREEN) {
      return MAX_SIZE_SCREEN;
    }
    return windowWidth;
  };
  const widthSize = getWidthSize();
  const textsClasses = textsStyles();
  const classes = serviceStyles();
  const [totalWidth, setTotalWidth] = useState(0);
  const [service, setService] = useState({});
  const [profile, setProfile] = useState({});
  const [rec, setRec] = useState([]);
  const [smallMultiplier, setSmallMultiplier] = useState(2);
  const [comments, setComments] = useState([]);
  const [profileIsOpen, setProfileIsOpen] = useState(false);
  const [scheduleIsOpen, setScheduleIsOpen] = useState(false);
  const [schedule, setSchedule] = useState([]);
  const [utcOffset, setUtcOffset] = useState('-05:00');

  useEffect(() => {
    const setData = async () => {
      const serviceData = await ServiceProvider.fetchById(id);
      setService(serviceData);
      setProfile(serviceData.profile);
      setComments(serviceData.comments);
      setSchedule(serviceData.schedule);
      setUtcOffset(serviceData.schedule[0].utc);
      const multiplierCard = parseInt((widthSize * 0.9) / CARD_WIDTH, 10);
      const multiplierSmallCard = multiplierCard * 2;
      setSmallMultiplier(multiplierSmallCard);
      setTotalWidth((multiplierCard * CARD_WIDTH) + ARROW_WIDTH);
      const recService = await RecommendationProvider.fetchAll();
      setRec(recService);
    };
    setData();
  }, [id, widthSize, service, comments]);

  const ratingStars = (rating) => {
    const stars = [];
    const yellowStar = parseInt(rating, 10);
    const greyStar = parseInt(TOTAL_RATING - rating, 10);
    const halfYellowStar = ((TOTAL_RATING - rating) - greyStar) === 0 ? 0 : 1;

    for (let i = 0; i < yellowStar; i += 1) {
      stars.push(star02);
    }
    for (let i = 0; i < halfYellowStar; i += 1) {
      stars.push(star03);
    }
    for (let i = 0; i < greyStar; i += 1) {
      stars.push(star01);
    }
    return (
      <div>
        {stars.map((star, key) => {
          return <img key={key} alt={star} src={star} />;
        })}
      </div>
    );
  };
  return (
    <Container>
      <Profile
        setIsOpen={setProfileIsOpen}
        isOpen={profileIsOpen}
        profile={profile}
      />
      <Handler
        setIsOpen={setScheduleIsOpen}
        isOpen={scheduleIsOpen}
        schedule={schedule}
        utcOffset={utcOffset}
      />
      <Grid container spacing={4}>
        {service.hasSale && (
        <Grid item xs={12}>
          <Paper elevation={0} className={classes.warningSale}>
            <ErrorIcon />
            <span className={textsClasses.heading5} style={{ margin: '0 10px' }}>
              {service.sale}
            </span>
            <span className={textsClasses.body2}>
              <FormattedMessage
                id="services.remain"
                defaultMessage="- quedan"
              />
              {' '}
              {service.remainingDays}
              {' '}
              <FormattedMessage
                id="services.days"
                defaultMessage="días"
              />
            </span>
          </Paper>
        </Grid>
        )}
        <Description
          item={service}
          windowWidth={windowWidth}
          ratingStars={ratingStars}
          profile={profile}
          setProfileIsOpen={setProfileIsOpen}
          setScheduleIsOpen={setScheduleIsOpen}
        />
        <Typography className={classes.grayLine} component="div" />
        <Comments
          comments={comments}
          ratingStars={ratingStars}
          windowWidth={windowWidth}
        />
        <Typography className={classes.grayLine} component="div" />
        <Grid item xs={12}>
          <Typography className={textsClasses.heading4} component="div">
            <FormattedMessage
              id="service.similar"
              defaultMessage="Servicios similares"
            />
          </Typography>
          <br />
          <Grid
            style={{ display: 'flex', justifyContent: 'center' }}
          >
            <Slider
              items={rec}
              totalWidth={totalWidth}
              multiplier={smallMultiplier}
              widthSize={widthSize}
              cardType="small"
              history={history}
            />
          </Grid>
        </Grid>
      </Grid>
    </Container>
  );
};

export default Service;
