import React from 'react';
import styled from 'styled-components';
import PropTypes from 'prop-types';
import { CardMedia } from '@material-ui/core';
import colors from '../utils/colors';
import { MEDIUM_CARD } from '../../constants';
import GlobalTheme from '../../styled/GlobalTheme';

const Container = styled.div`
  display: flex;
  width: ${MEDIUM_CARD};
  height: 200px;
  flex-direction: row;
  box-shadow: 0px 3px 15px rgba(0, 0, 0, 0.1);
  border-radius: 8px;
  text-align: left;
  margin: 15px;
  cursor: pointer;
  transform: ${({ type, translate }) => (type === 'left' ? `translateX(${-translate}px)` : `translateX(${translate}px)`)};
  transition: ${({ transition }) => `transform ease-out ${transition}s`};
  &:hover {
    box-shadow: 0px 3px 15px rgba(0, 0, 0, 0.25);
  }
  ${GlobalTheme.breakpoints.down('md')}{
    width: 360px;
    height: 141px;
  }
  ${GlobalTheme.breakpoints.down('sm')}{
    width: 210px;
  }
`;

const CardMediaStyled = styled(CardMedia)`
  && {
    width: 255px;
    height: 100%;
    border-radius: 8px 0 0 8px;
    ${GlobalTheme.breakpoints.down('md')}{
      width: 195px;
    }
    ${GlobalTheme.breakpoints.down('sm')}{
      display: none;
    }
  }
`;
const CardText = styled.div`
  width: 215px;
  padding: 20px;
  font-size: 20px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  box-sizing: content-box;
  ${GlobalTheme.breakpoints.down('md')}{
    width: 165px;
  }
  ${GlobalTheme.breakpoints.down('sm')}{
    width: 100%;
  }
`;

const CardFooter = styled.div`
  display: flex;
  justify-content: space-between;
`;

const Text = styled.p`
  color: ${({ color }) => color};
  font-weight: 600;
  margin: 0;
  ${GlobalTheme.breakpoints.down('md')}{
    font-size: 18px;
  }
`;

const SmallText = styled.p`
  color: ${({ color }) => color};
  font-size: 14px;
  margin: 0;
  ${GlobalTheme.breakpoints.down('md')}{
    font-size: 12px;
  }
`;

const MediumCard = ({ item, effect, history }) => {
  const { translate, transition, type } = effect;
  const redirectService = (id) => {
    history.push(`/service/${id}`);
  };
  return (
    <Container
      key={item.id}
      onClick={() => redirectService(item.id)}
      translate={translate}
      type={type}
      transition={transition}
    >
      <CardMediaStyled
        image={item.url}
        title="image"
      />
      <CardText>
        <Text color={colors.text02}>
          {item.name}
        </Text>
        <Text>
          {item.description}
        </Text>
        <CardFooter>
          <SmallText color={colors.ui05}>
            {item.userFullName}
          </SmallText>
          <SmallText color={colors.blueBrand}>
            {`Quedan ${item.remainingDays} días`}
          </SmallText>
        </CardFooter>
      </CardText>
    </Container>
  );
};

MediumCard.propTypes = {
  item: PropTypes.shape({
    id: PropTypes.number,
    name: PropTypes.string,
    url: PropTypes.string,
    description: PropTypes.string,
    userFullName: PropTypes.string,
    remainingDays: PropTypes.number,
  }).isRequired,
  effect: PropTypes.shape({
    translate: PropTypes.number,
    transition: PropTypes.number,
    type: PropTypes.string,
  }).isRequired,
  history: PropTypes.instanceOf(Object).isRequired,
};


export default MediumCard;
