import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import { FormattedMessage } from 'react-intl';
import {
  Grid,
  Typography,
  Button,
  Avatar,
} from '@material-ui/core';
import serviceStyles from './serviceStyles';
import textsStyles from '../utils/textsStyles';
import colors from '../utils/colors';

const Comments = (props) => {
  const {
    ratingStars,
    comments,
    windowWidth,
  } = props;
  const textsClasses = textsStyles();
  const classes = serviceStyles();
  const [commentHeight, setCommentHeight] = useState('170px');
  const [showComments, setShowComments] = useState(false);
  const [buttonText, setButtonText] = useState({
    id: 'services.moreComments',
    defaultMessage: 'Ver más comentarios',
  });
  const [typeOverflow, setTypeOverflow] = useState('hidden');
  const [minComments, setMinComments] = useState([]);
  const [allComments, setAllComments] = useState([]);
  const [items, setItems] = useState([]);
  useEffect(() => {
    setAllComments(comments);
    const totalComments = comments.length;
    const minCommentLength = totalComments === 1 ? 1 : 2;
    const lessComments = comments.slice(0, minCommentLength);
    setMinComments(lessComments);
    setItems(lessComments);
  }, [comments]);

  const showMoreComments = () => {
    setShowComments(!showComments);
    if (!showComments === true) {
      setCommentHeight('340px');
      setButtonText({
        id: 'services.lessComments',
        defaultMessage: 'Ver menos comentarios',
      });
      setTypeOverflow('scroll');
      setItems(allComments);
    } else {
      setCommentHeight('170px');
      setButtonText({
        id: 'services.moreComments',
        defaultMessage: 'Ver más comentarios',
      });
      setTypeOverflow('hidden');
      setItems(minComments);
    }
  };

  return (
    <Grid item xs={12}>
      <Typography className={textsClasses.heading4} component="div">
        <FormattedMessage
          id="service.comments"
          defaultMessage="Comentarios"
        />
      </Typography>
      <br />
      {comments.length > 0 && (
      <>
        <Typography
          style={{
            height: commentHeight,
            transition: 'height 300ms ease-out',
            overflow: typeOverflow,
          }}
          component="div"
        >
          {items.map((item, key) => (
            <Typography
            // eslint-disable-next-line react/no-array-index-key
              key={key}
              component="div"
              className={`${classes.flex} ${classes.space}`}
              style={{
                flexDirection: 'row',
                marginBottom: '38px',
              }}
            >
              <Typography
                className={classes.flex}
                style={{
                  alignItems: 'center',
                  maxWidth: '700px',
                }}
                component="div"
              >
                <Typography
                  className={classes.flex}
                  style={{
                    alignItems: 'center',
                    width: '200px',
                  }}
                  component="div"
                >
                  <Avatar alt="Remy Sharp" src={item.url} className={classes.spacing2} />
                  <Typography
                    className={textsClasses.heading5}
                    component="div"
                  >
                    {item.fullName}
                  </Typography>
                </Typography>

                <Typography
                  style={{
                    width: '500px',
                    marginLeft: '50px',
                  }}
                  component="div"
                >
                  <Typography
                    className={textsClasses.body3}
                    style={{ color: colors.text02 }}
                    component="div"
                  >
                    {item.body}
                  </Typography>
                  <Typography
                    className={textsClasses.body2}
                    style={{ color: colors.text03 }}
                    component="div"
                  >
                    {item.createdDate}
                  </Typography>
                  { windowWidth <= 1260 && (
                    <Typography
                      component="div"
                      className={classes.flex}
                      style={{
                        alignItems: 'center',
                      }}
                    >
                      {ratingStars(item.rating)}
                    </Typography>
                  )}
                </Typography>
              </Typography>
              { windowWidth > 1260 && (
                <Typography
                  component="div"
                  className={classes.flex}
                  style={{
                    alignItems: 'center',
                  }}
                >
                  {ratingStars(item.rating)}
                </Typography>
              )}
            </Typography>
          ))}
        </Typography>
        <Typography
          className={classes.alignCenter}
          style={{ marginTop: '38px' }}
          component="div"
        >
          <Button
            variant="outlined"
            className={classes.marginButtons}
            onClick={showMoreComments}
            color="secondary"
          >
            <FormattedMessage
              id={buttonText.id}
              defaultMessage={buttonText.default}
            />
          </Button>
        </Typography>
      </>
      )}
      {comments.length === 0 && (
        <Typography
          component="div"
          style={{ textAlign: 'center', color: colors.text03 }}
        >
          <FormattedMessage
            id="service.comments.noComments"
            defaultMessage="Aún no hay comentarios en este perfil"
          />
        </Typography>
      )}
    </Grid>
  );
};

Comments.propTypes = {
  comments: PropTypes.instanceOf(Array).isRequired,
  ratingStars: PropTypes.func.isRequired,
  windowWidth: PropTypes.number.isRequired,
};

export default Comments;
