import React, { useState } from 'react';
import { withStyles } from '@material-ui/core/styles';
import { FormattedMessage } from 'react-intl';
import {
  Typography,
  Button,
} from '@material-ui/core';
import styled from 'styled-components';
import MuiDialogContent from '@material-ui/core/DialogContent';
import PropTypes from 'prop-types';
import textsStyles from '../utils/textsStyles';
import selectedCardStyles from './selectedCardStyles';
import colors from '../utils/colors';
import backCard from '../../images/backCard.svg';
import GlobalTheme from '../../styled/GlobalTheme';

const CardImageBox = styled.div`
  position: relative;
  width: 360px;
  height: 207px;
  margin: 20px 0;
  ${GlobalTheme.breakpoints.down('sm')}{
    height: auto;
  }
`;

const CardImage = styled.img`
  ${GlobalTheme.breakpoints.down('sm')}{
    width: 275px;
  }
`;

const DialogContent = withStyles((theme) => ({
  root: {
    padding: '35px 50px',
    textAlign: 'center',
    position: 'relative',
  },
}))(MuiDialogContent);

const BackCard = (props) => {
  const {
    setTab,
    creditCardInfo,
    setCreditCardInfo,
  } = props;
  const textsClasses = textsStyles();
  const classes = selectedCardStyles();
  const [cvc, setCvc] = useState('');
  const [isActiveCvc, setIsActiveCvc] = useState(false);
  const handleContinue = () => {
    setCreditCardInfo({
      ...creditCardInfo,
      cvc,
    });
    setTab(8);
  };
  const handleChangeCVC = (event) => {
    const { value } = event.target;
    if (/^(\s*|\d+)$/.test(value) && value.length <= 3) {
      setCvc(value);
      setIsActiveCvc(value.length === 3);
    }
  };
  return (
    <DialogContent className={classes.cardGrid}>
      <Typography className={textsClasses.heading4} style={{ textAlign: 'center' }} component="div">
        <FormattedMessage
          id="schedule.card.register"
          defaultMessage="Registrar Tarjeta"
        />
      </Typography>
      <Typography
        className={textsClasses.body3}
        style={{ textAlign: 'center', color: colors.text02 }}
        component="div"
      >
        <FormattedMessage
          id="schedule.card.details"
          defaultMessage="Introduce los detalles de tu tarjeta"
        />
      </Typography>
      <CardImageBox>
        <CardImage
          alt="Back card"
          src={backCard}
        />
      </CardImageBox>
      <Typography
        component="div"
        style={{ width: '300px' }}
      >
        <Typography
          className={textsClasses.body5}
          component="div"
        >
          <FormattedMessage
            id="schedule.card.cvv"
            defaultMessage="Código de seguridad (CVV)"
          />
        </Typography>
        <Typography
          component="div"
          style={{ display: 'flex', justifyContent: 'center' }}
        >
          <input
            type="text"
            name="cvc"
            onChange={handleChangeCVC}
            placeholder="_ _ _"
            className={`${classes.inputCvc} ${textsClasses.body5}`}
            value={cvc}
          />
        </Typography>
      </Typography>
      <Button
        variant="contained"
        onClick={handleContinue}
        style={{
          margin: '35px 0px',
          padding: '10px 32px',
          color: colors.textInverse,
          backgroundColor: (
            isActiveCvc
          )
            ? colors.blueSkyBrand
            : colors.noSelectedBSB,
        }}
        className={textsClasses.heading5}
        color="primary"
        disabled={!isActiveCvc}
      >
        <FormattedMessage
          id="global.continue"
          defaultMessage="Continuar"
        />
      </Button>
    </DialogContent>
  );
};

BackCard.propTypes = {
  setTab: PropTypes.func.isRequired,
  creditCardInfo: PropTypes.instanceOf(Object).isRequired,
  setCreditCardInfo: PropTypes.func.isRequired,
};

export default BackCard;
